@import "_section.sass";

.container {
  max-width: 1190px;
  padding: 0 10px;
  margin: 0 auto;
  width: 100%; }
img {
  max-width: 100%; }
.button {
  cursor: pointer;
  background-color: $accent;
  color: #000;
  font-weight: 600;
  text-decoration: none;
  padding: 13px 45px;
  @include em(14);
  letter-spacing: 0.6px;
  text-transform: uppercase; }

.social {
  display: flex;
  justify-content: space-between;
  align-items: center;
  &__item {
    margin-right: 15px;
    &:last-child {
      margin-right: 0; }
    a {
      color: #ffffff;
      transition: all .25s;
      @include em(19);
      &:hover {
        color: $accent; } } } }
@import "_header.sass";
@import "_slider.sass";
@import "mouse";
@import "video";
