.section {
  &_full {
    min-height: 100vh; }
  &_dark {
    background-color: #0c0c0c; }
  &_relative {
    position: relative; }
  &_first {
    display: flex;
    align-items: stretch;
    background-image: url(../img/tuman.png);
    background-repeat: no-repeat;
    -webkit-background-size: cover;
    background-size: cover;
    .container {
      display: flex;
      justify-content: space-between;
      flex-direction: column;
      position: relative;
      z-index: 2; }
    &::before {
      content: '';
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      background-image: url(../img/elips.png);
      max-width: 430px;
      width: 100%;
      background-repeat: no-repeat;
      background-size: 100%;
      opacity: 0.05;
      transition: all .25s; } } }



.first__gtp {
  position: absolute;
  top: 35%;
  left: 50%;
  transform: translateY(-50%) translateX(-50%);
  @include em(200);
  font-weight: bold;
  color: #161616; }
@media screen and (min-width: 1170px) {
  .section {
    &_first {
      &::before {
        animation: sectionelips 2s linear 0s infinite normal forwards; } } } }
@keyframes sectionelips {
    0% {
        opacity: 0.15; }
    50% {
        opacity: 0.05; }
    100% {
        opacity: 0.15; } }
