.slider {
  &__item {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    position: relative;

    z-index: 4; }
  &__left {
    flex-basis: 45%;
    position: relative;
    height: 380px;
    display: flex;
    justify-content: flex-end;
    flex-direction: column; }
  &__right {
    flex-basis: 42%;
    position: relative;
    img {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      display: none; } }
  &__nav {
    flex-basis: 10%;
    text-align: right;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: flex-end; }

  &__name {
    color: #c3c3c3;
    @include em(18);
    text-transform: uppercase;
    font-weight: 500;
    letter-spacing: 4.5px; } }

.slide {
  display: none;
  position: absolute;
  top: 0;
  &__title {
    font-weight: 800;
    color: #ffffff;
    font-family: "Raleway", sans-serif;
    @include em(52);
    line-height: 64px;
    padding: 0;
    margin: 0; }
  &__text {
    color: #c3c3c3;
    @include em(14);
    font-weight: 300; }
  &__footer {
    display: flex;
    justify-content: flex-start;
    align-items: center; }
  &__video {
    display: flex;
    margin-left: 25px;
    justify-content: space-between;
    align-items: center;
    span {
      -webkit-border-radius: 10em;
      -moz-border-radius: 10em;
      border-radius: 10em;
      width: 55px;
      height: 55px;
      background-color: #000;
      transition: all .25s;
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer;
      svg {
        stroke: #fff;
        width: 15px;
        position: relative;
        left: 2px;
        transition: all .25s; } }
    p {
      padding-left: 10px;
      cursor: pointer;
      @include em(14);
      color: #fff;
      transition: all .25s; }
    &:hover {
      span {
        svg {
          stroke: $accent; } }
      p {
        color: $accent; } } }

  &__event {
    position: absolute;
    left: 85%;
    top: -10%;
    text-align: center;
    width: 148px;
    z-index: 3;
    background-image: url(../img/event.png);
    height: 152px;
    display: flex;
    align-items: center;
    justify-content: center;
    p {
      position: relative;
      z-index: 4;
      top: -5px;
      span {
        font-weight: 600;
        @include em(14); }
      .big {
        font-weight: 800;
        @include em(20); }
      .white {
        color: #ffffff; }
      .dark {
        color: #000; } } }

  &__button {
    svg {
      fill: #fff;
      cursor: pointer;
      width: 45px;
      opacity: .45;
      transition: all .25s;
      &:hover {
        opacity: 1; } }
    &.close {
      svg {
        opacity: .45; } }
    &_prev {
      svg {
        transform: rotate(180deg) rotateX(180deg); } }
    &_next {
      svg {
        transform: rotateX(180deg); } } }
  &__info {
    padding: 3vh 0;
    color: #fff; }
  &__active {
    @include em(22);
    font-weight: 600; }
  &__last {
    font-weight: 500; }
  &__line {
    height: 1px;
    background-color: rgba(#fff, .15);
    width: 65px;
    margin-left: auto;
    margin-top: 5px;
    span {
      display: block;
      width: 0;
      opacity: 1;
      height: 1px;
      background-color: $accent;
      transition: all 5s linear; } } }
